.projectFrame {
    display: flex;
    width: 18rem;
    height: 15rem;
    justify-content: space-between;
    flex-direction: column;
    cursor: default;
}

.title {
    color: #666;
    margin: 1rem;
}

@media ( max-width: 31.25rem ) {
    .projectFrame {
        width: 100%;
        align-content: stretch;
    }
}